/* eslint-disable camelcase */
import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';
import { Carousel } from 'react-responsive-carousel';
import { toast } from 'react-toastify';
import { push, goBack } from 'react-router-redux';
import './MarketPlace.css';
import ShowMoreText from 'react-show-more-text';
import ZoomImage from '../../../common/Components/ZoomImage';
import Image from '../../../common/Components/Image';
import Loader from '../../../common/Components/Loader';
import { GET_RESELLING_PRODUCTS, SET_PRODUCTS, SET_PUBLIC_PAGE_PRODUCTS } from '../../../constants/actionTypes';
import { APP_NAME } from '../../../constants/otherConstants';
import agent from '../../../agent';
import defaultLogo from '../../../assets/Images/instalink_logo.png';

class ResellingProduct extends Component {
  constructor(props) {
    super(props);
    this.state = {
      product: { product: {}, seller: {} },
      loading:false,
    };
    this.chooseProduct = this.chooseProduct.bind(this);
  }

  componentDidMount() {
    const { product } = this.props;
    if (product) {
      this.setState({ product });
    }
  }

  componentWillReceiveProps(np) {
    const { product } = this.props;
    if (np.product && np.product !== product) {
      this.setState({ product: np.product });
    }
  }

  chooseProduct() {
    const { product } = this.state;
    const { id } = product.product;
    const data = {
      product_id: id,
    };
    const {
      products, setProducts, publicPageProducts, setPublicPageProducts, chosenCategory, publicPageChosenCategory,
    } = this.props;
    this.setState({loading: true})
    agent.ProductsPage.addResaleProductToShop(data).then((res) => {
      toast.info('Product Added to Resale');
      this.setState({loading: false})
      if (!chosenCategory || chosenCategory === product.product.category.name) {
        setProducts({ products: [product.product, ...products] });
      }
      if (!publicPageChosenCategory || publicPageChosenCategory === product.product.category.name) {
        setPublicPageProducts({ products: [product.product, ...publicPageProducts] });
      }
    }).catch((err) => {
        this.setState({loading: false})
      console.log(err, err.response);
      if (err.response && err.response.data.error) {
        toast.error(err.response.data.error);
      } else {
        toast.error('Error occured in adding product');
      }
    });
  }

  render() {
    const { loading } = this.state;
    const { product, seller,  } = this.state.product;
    const {
      description, disc_price, images, name, preview_images, resell_margin,
    } = product;

    return (
      <div className="reselling-product-div">
        <div className="product-header">
          <div className="seller-div">
            <div className="seller-logo">
              <img src={seller.header_icon || defaultLogo} alt="" />
            </div>
            <div className="seller-name">
              {seller.header_text || APP_NAME}
            </div>
          </div>
          <div className="margin">
            {`margin: ${resell_margin} Rs`}
          </div>
        </div>
        <div className="product-img-carousel">
          <Carousel className="" style={{ background: 'white' }} showArrows infiniteLoop showIndicators={false} showThumbs={false}>
            { images ? images.map((img, i) => (
              <ZoomImage>
                <Image src={img} alt="" overlaySrc={preview_images[i]} key={i} />
              </ZoomImage>
            )) : null}
          </Carousel>
        </div>
        <div className="product-footer">
          <div className="content">
            <div>
              <b>{name}</b>
              <p>{`${disc_price} Rs.`}</p>
              <ShowMoreText
                lines={1}
                more="See more"
                less="See less"
                anchorClass="show-more"
                expanded={false}
                width={150}
              >
                <small>
                  {description}
                </small>
              </ShowMoreText>

            </div>


          </div>
          <button type="button" className='relative' onClick={this.chooseProduct}>
            Resale Product

          </button>
        </div>
        {
            loading?
            <Loader/>
            :null
        }

      </div>
    );
  }
}


const mapStateToProps = (state) => ({
  products: state.ProductsPageReducers.products,
  productsPageNum: state.ProductsPageReducers.productsPageNum,
  publicPageProducts: state.HomePageReducers.publicPageProducts,
  chosenCategory: state.ProductsPageReducers.chosenCategory,
  publicPageChosenCategory: state.HomePageReducers.publicPageChosenCategory,
});

const mapDispatchToProps = (dispatch) => ({
  getResellingProducts: (pageNumber) => dispatch({ type: GET_RESELLING_PRODUCTS, pageNumber, payload: agent.ProductsPage.getResellingProducts(pageNumber) }),
  setProducts: (products) => dispatch({ type: SET_PRODUCTS, payload: products }),
  setPublicPageProducts: (products) => dispatch({ type: SET_PUBLIC_PAGE_PRODUCTS, payload: products }),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(ResellingProduct);


